import React, { createContext } from "react"
import FuncCategory from "./Api/FuncCategory"
import { checker } from "../utils"
import { useLocation, useOutletContext, useParams } from "react-router-dom"
import { Helmet } from "react-helmet"

import categories from "../functions.json"
const TagsContext = createContext()

export { TagsContext };

export default function Documentation() {
    const [selectedTags, setSelectedTags] = React.useState([])
    const searchQuery = useOutletContext()
    const [filters, setFilters] = React.useState({
        And: false,
        Or: false
    })
    let params = useParams()
    const { hash } = useLocation()
    const selectFunction = hash.substring(1)
    //console.log(selectFunction)
    let selectFunctionObj = {}
    //const filters = ["and", "or"]
    let tags = []
    let tagCounts = []
    const noMatches = <h1 
        className="white"
    >
        No matches for 
        {searchQuery !== "" ? ` \"${searchQuery}\"` : ""}
        {searchQuery !== "" && selectedTags.length > 0 ? " and " : ""}
        {selectedTags.length > 0 ? "selected tags" : ""}
    </h1>

    const categoryElements = categories.map(({ category, description, functions }) => {

        if (selectFunction !== "") {
            const filteredFunctions = functions.filter(func => func.name.toLowerCase() === selectFunction.toLowerCase())
    
            selectFunctionObj = filteredFunctions.length > 0 ? filteredFunctions[0] : selectFunctionObj
        }

        if (selectedTags.length > 0 || searchQuery !== "") {
            functions = functions.filter(func => {
                const matchesSearch = searchQuery !== ""
                    ? func.name.toLowerCase().includes(searchQuery.toLowerCase())
                    : false

                const matchesTags = func.tags?.length > 0
                    ? checker(func.tags, selectedTags)
                    : false

                return matchesSearch || matchesTags
            });
        }

        // gathering tags
        functions.map(func => {
            tags = tags.concat(func.tags).filter((value, index, self) => {
                return value !== undefined ? self.indexOf(value) === index : null
            })
        })

        // gathering tag counts for each tag
        functions.map(func => {
            (func.tags && (selectedTags == 0 || selectedTags.every(value => func.tags.includes(value)))) && func.tags.map(tag => {
                tagCounts[tag] = (tagCounts[tag] || 0) + 1
            })
        })

        if (functions.length > 0) {
            return (
                <FuncCategory
                    name={category}
                    description={description}
                    functions={functions}
                    updateTags={updateTags}
                />
            )
        }

        return false

    }).filter(element => element !== false)

    tags.sort()

    function updateTags(tag) {
        setSelectedTags(prevSelectedTags => {
            const index = prevSelectedTags.indexOf(tag)
            if (index > -1) {
                const length = prevSelectedTags.length
                //console.log(`Removed tag ${tag} length ${length}`)
                return prevSelectedTags.filter(selected => selected !== tag);
            } else {
                //console.log(`Added tag ${tag}`)
                return [...prevSelectedTags, tag]
            }
        })
    }

    function sortKeys(object) {
        let sortedObj = []
        Object.keys(object).sort().forEach(key => {
            sortedObj[key] = object[key];
        });

        return sortedObj
    }

    function countTag(tag) {

    }

    function updateFilter(filter) {
        setFilters({
                And: false,
                Or: false,
                [filter]: true
        })

        console.log(filters)
    }

    //console.log(filters)

    const filterElements = Object.entries(filters).map(([key, value]) => {
        const selected = value
        return (<span className={`func--prev ${selected ? "red" : ""}`} onClick={() => updateFilter(key)}>{key}</span>)
    })

    const tagElements = tags.map(tag => {
        const index = selectedTags.indexOf(tag)
        const selected = index > -1
        const tagCount = tagCounts[tag]
        const matches = tagCount > 0
        const styled = { color: `${matches ? "limegreen" : "red"}` }
        return (
            tagCount > 0
            ? <span
                className={`func--prev`}
                onClick={() => updateTags(tag)}
            >
                <inline className={`${selected ? "red" : ""}`} >{tag}</inline>
                <inline class="tag--count" >{tagCount || 0}</inline>
            </span>
            : ""
        )
    })

    //console.log(tags)
    //console.log(selectedTags)
    //console.log(categoryElements)

    return (
        <TagsContext.Provider value={{ updateTags, selectedTags }}>
            <Helmet>
                <title>{selectFunction}</title>
                <meta name="description" content={`This is a link about ${selectFunction}`} />
                <meta name="og:description" content={`This is a link about ${selectFunction}`} />
            </Helmet>
            <div className="func--background">
                {/*<h3 style={{color: "white", padding: "none", margin: "none"}}>Tag filtering:</h3>
                <div className="func--flex func--tags">
                    {filterElements}
                </div>*/}
                <h3 className="white" >Available tags:</h3>
                <div className="func--flex func--tags">
                    {tagElements}
                </div>
                <div>
                    {categoryElements.length > 0
                        ? categoryElements
                        : noMatches}
                </div>
            </div>
        </TagsContext.Provider>
    )
}