import React, { useContext } from "react"
import { Link } from "react-router-dom"
import { TagsContext } from "../Api"

export default function Func({ tags, name, description, inputs, outputs, code, deprecateNotice, status, example, table }) {
    const { updateTags, selectedTags } = useContext(TagsContext)
    const apiLink = "https://teardowngame.com/modding/api.html#"
    const inOutStyle = 
        inputs.length > 0 || outputs.length > 0
            ? inputs.length > 1 && outputs.length > 1
                ? `"returns arguments"`
                : inputs.length > 1
                    ? `"returns returns"
            "arguments arguments"`
                    : `"arguments arguments"
            "returns returns"`
            : `"returns arguments"`

    const funcStyle = {
        gridTemplateAreas: `
            "header header"
            "example example"
            ${inOutStyle}
            "description description"
            "table table"
            "code code"
            "tags tags"
        `,
    }

    tags?.sort()
    
    const tagElements = tags?.map((tag, index) => {
        const selected = selectedTags.includes(tag)
        return (
            <span
                key={index}
                className={`func--prev ${selected ? "red" : ""}`}
                onClick={() => updateTags(tag)}
            >{tag}</span>
        )
    })

    const outputsExamples = outputs.map((ret, index) => (
        <span key={index} className="func--return courier">
            {ret.name}
            {index < outputs.length - 1 && <inline className="func--text">, </inline>}
        </span>
    ))

    const inputsExamples = inputs.map((arg, index) => (
        <span key={index} className="func--argument courier">
            {arg.optional ? `[${arg.name}]` : arg.name}
            {index < inputs.length - 1 && <inline className="func--text func--func">, </inline>}
        </span>
    ))

    const inputsList = inputs.map((arg, index) => (
        <p key={index} className="func--argument courier">{arg.name}
            <inline className="func--text" style={{ marginLeft: "5px"}} >
                (<inline className="courier">
                    {arg.type}
                </inline>
                {arg.optional &&  ", optional"}
                ) - {arg.description}
            </inline>
        </p>
    ))

    const outputsList = outputs.map((ret, index) => (
        <p key={index} className="func--return courier">{ret.name}
            <inline className="func--text" style={{ marginLeft: "5px"}} >
                (<inline className="courier">
                    {ret.type}
                </inline>) - {ret.description}</inline>
        </p>
    ))
    
    const exampleReturns = (outputs[0] 
        ? <>{outputsExamples}<span className="func--text"> = </span></> 
        : ""
    )
    const exampleArguments = (inputs[0] 
        ? <span className="func--argument">{inputsExamples}</span> 
        : ""
    )

    const headerElement = (
        <h2 className="func--header">
            {name}
            {status && <inline className="func--text func--status">{` (${status})`}</inline>}
        </h2>)

    const exampleElement = (
        <div className="func--example">
            {exampleReturns}
            <span className="func--text func--func courier">{`${name}(`}</span>
            {exampleArguments}
            <span className="func--text func--func courier">)</span>
        </div>)

    const descriptionElement = (
        <div className="func--description">
            {deprecateNotice && <p className="red bold">This function will be deprecated in the next update!</p>}
            {description && <p className="func--text">{description}</p>}
        </div>)

    const returnsElement = (
        <div className="func--returns">
            <p className="func--text">Return values</p>
            {
                outputs[0]
                    ? <>{outputsList}</>
                    : <p className="func--return">none</p>
            }
        </div>)

    const tagField = (
        <div className="func--flex func--tags">
            {tagElements}

            {<inline className={`func--prev`}>
                <Link 
                to={`${apiLink}${name}`} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="api-link"
            >
                    Official API
                </Link>
            </inline>}
        </div>
    )

    const argumentsElement = (
        <div className="func--arguments">
            <p className="func--text">Arguments</p>
            {
                inputs[0]
                    ? <>{inputsList}</>
                    : <p className="func--argument">none</p>
            }
        </div>)

    const exampleCode = example
        ? <div className="func--code func--text courier">
            {example}
        </div>
        : ""

    const tableHeaders = table
        ? (
            <thead>
                <tr>
                    {table[0]?.headers.map(header => {
                        return (
                            <th key={header} >
                                {header}
                            </th>
                        )
                    })}
                </tr>
            </thead>
        )
        : ""
    const tableData = table
        ? (
            <tbody>
                {table[0]?.data.map(data => {
                    const content = data.map(content => {
                        return (
                            <td key={content} >
                                {content}
                            </td>
                        )
                    })

                    return (
                        <tr key={data[0]}>
                            {content}
                        </tr>
                    )
                })}
            </tbody>
        )
        : ""

    const tableElement = table 
        ? <div className="func--table">
            <table className="func--text">
                {tableHeaders}
                {tableData}
            </table> 
        </div>
        : ""

    return (
        <div className="func" style={funcStyle}>
            {headerElement}
            
            {exampleElement}

            {returnsElement}

            {argumentsElement}

            {descriptionElement}

            {tableElement}

            {exampleCode}
            
            {tagField}
        </div>
    )
}